body{
  margin: 0;
  padding: 0;
}

.nav-gallery{
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #48b;
  padding: 5px;
  border-bottom: 1px solid #000;
}

.nav-gallery h2{
  color: #fff;
  font-size: 35px;
  margin-left: 15px;
}

.form{
  width: 250px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px;
}

.input-text{
  width: 200px;
  outline: none;
  background-color: #ccc;
  color: #000;
  padding: 3px;
  border: 1px solid #48b;
}

.btn-img{
  background-color: #48b;
  color: #fff;
  margin-left: 3px;
  padding: 5px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.container{
  padding: 15px;
  background-color: #ccc;
}

.row-img{
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: 200px;
  padding: 15px;
  grid-gap: 10px;
  background-color: #ccc;
  border: 1px solid #000;
}

.card-img{
  width: 100%;
  height: 100%;
  object-fit: cover;
  border: 1px solid #ccc;
}

.card-img:last-child{
  grid-column: span 2;
}

.footer{
  background-color: #48b;
  padding: 5px;
  border-top: 1px solid #000;
}

.footer p{
  color: #ccc;
  margin-left: 10px;
}

.loader,
.loader:after {
  border-radius: 50%;
  width: 10em;
  height: 10em;
}
.loader {
  margin: 60px auto;
  font-size: 10px;
  position: relative;
  text-indent: -9999em;
  border-top: 1.1em solid rgba(255, 255, 255, 0.2);
  border-right: 1.1em solid rgba(255, 255, 255, 0.2);
  border-bottom: 1.1em solid rgba(255, 255, 255, 0.2);
  border-left: 1.1em solid #48b;
  -webkit-transform: translateZ(0);
  -ms-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-animation: load8 1.1s infinite linear;
  animation: load8 1.1s infinite linear;
}
@-webkit-keyframes load8 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes load8 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
